import React, { useEffect } from "react";
import Card from "../../UI/Card";
import styles from "../styles.module.css";
import { FiCheckCircle } from "react-icons/fi";
import { USER_CLOCKIN_RESET } from "../../../constants/userConstants";
import { useDispatch, useSelector } from "react-redux";

const ClockinSuccess = ({ history }) => {
  const dispatch = useDispatch();

  const userClockin = useSelector((state) => state.userClockin);
  const { clockin } = userClockin;

  useEffect(() => {
    setTimeout(() => {
      dispatch({ type: USER_CLOCKIN_RESET });
      history.push("/attendance");
    }, 5000);
  }, [history, dispatch]);
  return (
    <div className={styles.app}>
      <Card title="Clockin" content="Success" />
      <div className={styles.form}>
        <h1 className="text-center text-success" style={{ fontSize: "10rem" }}>
          <FiCheckCircle />
        </h1>
        <h3 className="text-center">{clockin}</h3>
      </div>
    </div>
  );
};

export default ClockinSuccess;
